.tarea-contenedor {
  width: 500px;
  min-height: 65px;
  background-color: #50727B;
  margin: 5px 0;
  padding: 8px 15px 8px 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-radius: 7px;
  border: 1px solid white;
  cursor: pointer;

}

/* Colores */

.tarea-contenedor:nth-child(3n + 1) {
  background-color: #70958B;
}

.tarea-contenedor:nth-child(3n + 2) {
  background-color: #50727B;
}

.tarea-contenedor:nth-child(3n + 3) {
  background-color: #30727B;
}

/* Texto */
.tarea-texto {
  width: 100%;
  height: 100%;
  font-size: 20px;
  display: flex;
  align-items: center;
  overflow-wrap:anywhere;   /*  Si el texto es muy largo va a sercortado y va a ir hacia abajo */
}

/* Icono */

.tarea-icono {
  width: 25px;
  height: 25px;
  margin: 5px;
}

.completada {
  background-color: #800020 !important;
  text-decoration: line-through;
}